@import '_utilities/_mixins';
@import '_utilities/_variables';
@import '_utilities/_helper';
@import '_partials/_navbar';
@import '_partials/_footer';
@import '_partials/_page-home';
html,body{
    height:100%;
}
body {
    font-family: 'Lato', sans-serif;
    color: $clr-white;
}

img {
    max-width: 100%;
}
.br-page-header{
    padding: 30px 30px 0 30px;
    margin: auto;
    @include position(absolute,0,0,auto,0);
}
.br-pagefooter{
    
    padding: 0 30px;
    margin: auto;
    @include position(absolute,auto,0,0,0);
}
.br-time-wrapper{
    font-weight: 300;
    font-size: 50px;
    .br-time{
    }
    .br-format{

        font-size: 40px;
    }
}
.br-date-wrapper{
    
    font-weight: 100;
    font-size: 22px;
}
.br-browser-support{
    span{

        font-size: 10px;    
        font-family: 'Montserrat', sans-serif;
        font-weight: 100;
        padding-right: 25px;
    }
    .fa-chrome{
        padding-right: 25px;
    }
}